<template>
	<view class="global">
		<view style="background-color: white;border-bottom: 1px solid #e6e6e6;">
		<uni-segmented-control :current="current" :values="items" @clickItem="onClickItem" styleType="text"
			activeColor="#05c0ab"></uni-segmented-control></view>
			<view v-show="current === 0">
				
				<view class="contents">
					<view class="line">
						<view class="lines">订单明细</view>
					</view>
					<view class="line">
						<view class="lines">{{orderlists.ShopName}}</view>
					</view>

					<view class="line" v-for="(item,index) in orderlist" :key="index">
						<view class="linea">
							<view>{{item.ProductName}}</view>
							<view class="quantity">*{{item.OrderCount}}</view>
							<view class="unit-price">￥{{item.RealOrderAmount}}</view>
						</view>
					</view>

					<view class="line">
						<view class="linea">
							<view>配送费</view>
							<view class="delivery-cost">￥{{orderlists.DeliveryAmount}}</view>
						</view>
					</view>

					<view class="line">
						<view class="linea">
							<view style="display: flex;">
								<view>原价￥{{orderlists.OrderAmount}}</view>
								<view class="discounts">共优惠￥{{orderlists.BenefitAmount}}</view>
							</view>
							<view style="display: flex;">
								<view>实付</view>
								<view class="delivery-cost">￥{{orderlists.FactPayAmount}}</view>
							</view>
						</view>
					</view>

					<view class="ddxx">订单信息</view>
					<view class="line">
						<view style="display: flex;">
							<view class="lines">订单编号：</view>
							<view class="order-number">{{orderlists.SerialNumber}}</view>
						</view>
					</view>
					<view class="line">
						<view style="display: flex;">
							<view class="lines">打印编号：</view>
							<view class="order-numbers">{{orderlists.PrintNo}} 【已下单{{orderlists.OrderTimes}}次】</view>
						</view>
					</view>
					<view class="line">
						<view style="display: flex;">
							<view class="lines">订单时间：</view>
							<view class="order-number">{{orderlists.OrderDate}}</view>
						</view>
					</view>
					<view class="line">
						<view style="display: flex;">
							<view class="lines">支付方式：</view>
							<view class="order-number">{{orderlists.PayTypeText}}</view>
						</view>
					</view>
					<view class="line">
						<view style="display: flex;">
							<view class="lines">配送方式：</view>
							<view class="order-number">{{orderlists.DeliveryTypeText}}</view>
						</view>
					</view>
					<view class="line">
						<view style="display: flex;">
							<view class="lines">配送地址：</view>
							<view class="order-number">{{orderlists.LinkMan}} {{orderlists.Telephone}} {{orderlists.Address}}</view>
						</view>
					</view>
					<view class="line">
						<view style="display: flex;">
							<view class="lines">联系买家：</view>
							<view class="order-number">{{orderlists.Telephone}}</view>
						</view>
					</view>
					<view class="line">
						<view style="display: flex;justify-content: space-between;">
							<view class="lines">联系不上买家：</view>
							<view style="display: flex;">
								<view>通知买家</view>
								<image src="../../static/image/rights.png" style="width: 30px;margin-top: -2px;"
									mode="widthFix"></image>
							</view>
						</view>
					</view>
					
				</view>
			</view>
			<view v-show="current === 1">
				<view style="height: 100vh;background-color: white;">
					<uni-steps
						:options="[{title:'买家已下单,等待买家付款',desc:'2022-07-11'},{title:'付款成功,商家已接单',desc:'2022-11-12'},{title:'交易完成',desc:'2018-11-14'}]"
						activeColor="green" direction="column" :active="1"></uni-steps>
				</view>
			</view>
		<!-- </view> -->

		<view class="btn">
			<view class="btns">
				<view class="bottoma">
					<view class="text">取消订单</view>
				</view>
				<view class="bottomb">
					<view class="text">打印订单</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				items: ['订单明细', '订单状态'],
				current: 0,
				
				orderlist:[],
				orderlists:{}
			}
		},
		onLoad() {
			var ddmx = uni.getStorageSync('data')
			this.orderlist = ddmx.Result.OrderDetailList;
			this.orderlists = ddmx.Result
		},
		methods: {
			onClickItem(e) {
				if (this.current != e.currentIndex) {
					this.current = e.currentIndex;
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
@import url('@/pages/order/order-detail.css');
</style>
